/* 

  url 
	method 
	data 
	loading   
 
 */
import base from './base.js'

export default function(url, data = {}, method = 'GET', loading = true) {
	return new Promise((resolve, rejects) => {
		//
		if (loading) {
			uni.showLoading({
				title: ""
			})
		}
		uni.request({
			url: base.host + url,
			method,
			data,
			success: (res) => {
				//
				if (res.data.status === 200) {
					uni.showLoading({
						title: ""
					})
					resolve(res.data)
				}
			},
			fail: (error) => {
				//
				rejects(error)
			},
			complete() {
				//
				uni.hideLoading()
			}
		})
	})
}
